import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Row, Col } from 'antd';
import InfoCountTotalCard from '../../../components/common/InfoCard/index';
import {
  getQuickLinkAcademicGetDisciplineNo,
  getQuickLinkAcademicGetDisciplineBehaviorNo,
  getQuickLinkAcademicGetDisciplineNoteNo,
} from '../../../services/quicklink';

const COLORS = [
  '#0088FE',
  '#00C49F',
  '#FFBB28',
  '#FF8042',
  '#0088fed9',
  '#00c49fd9',
  '#ffbb28d9',
  '#ff8042d9',
];

const data = [
  {
    title: 'Loại xử lý kỷ luật',
    service: getQuickLinkAcademicGetDisciplineNo,
    url: '/typeDisciplines/list',
  },
  {
    title: 'Hành vi kỷ luật',
    service: getQuickLinkAcademicGetDisciplineBehaviorNo,
    url: '/typeDisciplineBehaviors/list',
  },
  {
    title: 'QĐ kỷ luật',
    service: getQuickLinkAcademicGetDisciplineNoteNo,
    url: '/discipline/add',
  },
];

class DashboardAcademic extends Component {
  state = {};
  render() {
    return (
      <Row gutter={24}>
        {data.map((item, index) => (
          <Col span={6} key={item.title}>
            <InfoCountTotalCard
              backgroundColor={COLORS[index % 8]}
              title={item.title}
              service={item.service}
              icon={item.icon}
              url={item.url}
            />
          </Col>
        ))}
      </Row>
    );
  }
}

export default props => (
  <Page {...props}>
    <DashboardAcademic {...props} />
  </Page>
);



